html {
  height: 100%;
  overflow-x: hidden;
}

body {
  min-width: 320px;
  color: #585a59;
  font-family: roboto-400, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  scroll-behavior: smooth;
  position: relative;
}

@media screen and (min-width: 481px) {
  body {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  body::-webkit-scrollbar {
    width: 5px;
  }
  body::-webkit-scrollbar-track {
    width: 5px;
  }
  body::-webkit-scrollbar-thumb {
    background-color: #222;
  }
}

body.loading .preloader {
  display: block;
}

header:after,
footer:after,
main:after,
div:after,
section:after,
ul:after,
ol:after {
  content: ' ';
  display: table;
  clear: both;
}

select {
  width: 100%;
  outline: none;
  background-color: transparent;
}

textarea {
  width: 100%;
  padding: 10px 15px;
  resize: none;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

textarea::-webkit-scrollbar {
  width: 5px;
}

textarea::-webkit-scrollbar-track {
  width: 5px;
}

textarea::-webkit-scrollbar-thumb {
  background-color: #222;
}

.windows {
  display: none;
}

.clearfix:after, .clr:after {
  content: ' ';
  display: table;
  clear: both;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 1200px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.overlay {
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.preloader {
  display: none;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10000;
}

.loader {
  animation: animate 1.5s linear infinite;
  clip: rect(0, 80px, 80px, 40px);
  height: 80px;
  width: 80px;
  position: fixed;
  top: 50vh;
  left: 50vw;
  margin-left: 100px;
}

.loader::after {
  animation: animate2 1.5s ease-in-out infinite;
  clip: rect(0, 80px, 80px, 40px);
  content: '';
  border-radius: 50%;
  height: 80px;
  width: 80px;
  position: absolute;
}

.loader__wrap {
  display: none;
  background-color: rgba(255, 255, 255, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.loader--scroll {
  width: 32px;
  height: 32px;
  background-image: url("../img/general/loader.gif");
  background-color: transparent;
  background-repeat: no-repeat;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -42px;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(220deg);
  }
}

@keyframes animate2 {
  0% {
    box-shadow: inset #ff8272 0 0 0 17px;
    transform: rotate(-140deg);
  }
  50% {
    box-shadow: inset #ff8272 0 0 0 2px;
  }
  100% {
    box-shadow: inset #ff8272 0 0 0 17px;
    transform: rotate(140deg);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 130, 114, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(255, 130, 114, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 130, 114, 0);
  }
}

.is-hidden {
  display: none !important;
}

.hidden {
  display: none;
}

.lg-only,
.md-only,
.sm-only,
.xs-only {
  display: none;
}

@media (max-width: 1200px) {
  .lg-only {
    display: block;
  }
  .lg-hide {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .md-only {
    display: block;
  }
  .md-hide {
    display: none;
  }
}

@media (max-width: 768px) {
  .sm-only {
    display: block;
  }
  .sm-hide {
    display: none;
  }
}

@media (max-width: 425px) {
  .xs-only {
    display: block;
  }
  .xs-hide {
    display: none !important;
  }
}

