  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.modal-footer > :not(:first-child) {
  margin-left: 0.25rem;
}

.modal-footer > :not(:last-child) {
  margin-right: 0.25rem;
}

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

@media (max-width: 768px) {
  .modal-dialog {
    max-width: 500px;
    margin: 20px auto;
  }
  .modal-dialog-centered {
    min-height: calc(100% - (20px * 2));
  }
  .modal-content {
    box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.5);
  }
  .modal-sm {
    max-width: 300px;
  }
}

@media (max-width: 1200px) {
  .modal-lg {
    max-width: 800px;
  }
}

.btn {
  padding: 6px 35px 7px;
  font-family: roboto-400, sans-serif;
  font-size: 15px;
  letter-spacing: 0.3px;
  text-align: center;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  position: relative;
  z-index: 1;
  transition: all 0.35s ease;
  display: inline-flex;
  justify-content: center;
}

.modal-open .modal {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.modal-open .modal::-webkit-scrollbar {
  width: 5px;
}

.modal-open .modal::-webkit-scrollbar-track {
  width: 5px;
}

.modal-open .modal::-webkit-scrollbar-thumb {
  background-color: #222;
}

@media (max-width: 425px) {
  .modal-dialog {
    margin-top: 60px;
  }
}

.modal-dialog--lg {
  max-width: 960px;
  margin: 20px auto;
}

.modal-dialog--md {
  max-width: 768px;
  margin: 20px auto;
}

.modal-dialog--sm {
  max-width: 425px;
  margin: 20px auto;
}

.modal-header {
  color: #fff;
  border-bottom: none;
  background-color: #ff8272;
  position: relative;
}

.modal-header .modal__btn--close {
  top: 8px;
  right: 5px;
}

.modal-title {
  margin: auto 0;
  font-family: roboto-400, sans-serif;
  font-weight: 500;
  font-size: 17px;
  letter-spacing: 0;
  text-transform: initial;
  line-height: 1;
}

.modal-footer {
  border-top: 1px solid #e8e8e8;
  background-color: #f5f5f5;
}

.modal-footer button {
  padding: 4px 20px;
  font-size: 14px;
}

.modal-close {
  height: auto;
  padding: 5px 10px;
  font-size: 21px;
  font-weight: 600;
  line-height: 1;
  color: #fff;
  text-shadow: none;
  background-color: transparent;
  box-shadow: none;
  border: none;
  outline: none;
  cursor: pointer;
  opacity: 0.5;
  position: absolute;
  top: 7px;
  right: 5px;
  z-index: 1;
}

.modal-close:hover {
  text-decoration: none;
  opacity: 1;
}

.modal-close::before {
  content: '\f00d';
  font-family: faL;
  font-size: 16px;
}

.modal-close:not(:disabled):not(.disabled) {
  cursor: pointer;
}
